.container {
  min-height: 60vh;
  display: grid;
}

.container .Spinner {
  width: 100px;
  height: 100px;
  border: 5px solid #bdbdbd;
  border-top-color: tomato;
  border-radius: 50%;
  place-self: center;
  animation: rotate 500ms infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
