.app-download{
    margin: auto auto;
    margin-top: 100px;
    font-size: max(3vw,20px);
    text-align: center;
}

.app-download-platforms{
    display: flex;
    justify-content: center;
    gap: max(2vw, 10px);
    margin-top: 40px;
}

.app-download-platforms img{
    width: max(30vw, 120px);
    max-width: 180px;
    transition: .5s;
    cursor: pointer;
}

