.footer {
  color: #d9d9d9;
  background-color: #323232;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 30px 8vw;
  padding-top: 80px;
  margin-top: 120px;
}

.footer-content {
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 80px;
}

.footer-content-center,
.footer-content-right,
.footer-content-left {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 20px;
}

.footer-content-center li,
.footer-content-right li,
.footer-content-left li {
  list-style: none;
  margin-bottom: 10px;
  cursor: pointer;
}

.footer-content-right h2,
.footer-content-center h2 {
  list-style: none;
  margin-bottom: 5px;
  cursor: pointer;
}

.footer-social-icons img {
  width: 40px;
  margin-right: 15px;
}

.footer hr {
  width: 100%;
  height: 2px;
  margin: 20px 0;
  background-color: grey;
  border: none;
}

@media (max-width:750px) {
  .footer-content{
    display: flex;
    flex-direction: column;
    gap: 35px;
  }

  .footer-copyright{
    text-align: center;
  }
}